/* portfolio section */
.each-portfolio {
    width: 75vw; max-width: 400px;
    border-radius: 5px;
} 
.portfolio-img {
    display: flex; align-items: center;
    justify-content: center;
    width: 100%; height: 60vw;
    max-width: 500px; max-height: 356px;
    padding: 10px;
    background-repeat: no-repeat; background-position: center center;
}
#text {
    text-align: center;
}

/* Learn more button */
.learnmore-button-notHover {
    font-size: 0; border: none
}
.learnmore-button-Hover {   
/* when mouse cursor is on portpolio section element */
    font-size: 20px;  color: rgb(39, 39, 39); font-weight: bold;
    border: 2px solid rgb(39, 39, 39);
    margin-top: 30px; margin-left: auto; margin-right: auto;
    width: 150px;
    padding: 5px;
    cursor: pointer;
}
.learnmore-button-Hover:hover {     
/* when mouse cursor is on button element
while mouse cursor is on portpolio section element*/
    background-color: rgb(150, 148, 148);
}

/* Responsive CSS */
@media screen and (max-width: 700px) {
    .portfolio-title { font-size: 20px; }
    .portfolio-stack-title { font-size: 17px;}
}
/* @media screen and (min-width: 430px) {
    .portfolio-img { height: 60vw; }
} */
@media screen and (max-width: 429px) {
    .portfolio-img { height: 80vw; }
    .portfolio-title { font-size: 20px;}
    .portfolio-stack-title { font-size: 15px; }
    .stacks { font-size: 12px; }
    .learnmore-button-Hover { font-size: 15px; width: 110px; padding: 3px; }
}
@media screen and (max-width: 310px) {
    .portfolio-img { height: 300px; }
    .portfolio-title { font-size: 15px;}
    .portfolio-stack-title { font-size: 12px; }
    .portfolio-stacks { font-size: 10px; }
    .learnmore-button-Hover { font-size: 10px; width: 70px; }
}
