/* contents */
#contact {
    background-color: hsl(233, 27%, 25%);
    display: flex; flex-direction: column;
    justify-content: center; align-items: center;
}
#contact-title {
    color: white
}
#contact-contents {
    text-align: center;
}
#contact-email {
    font-size: 20px;
    color: #d6d5d5;
}
.contact-logo { width: 50px; height: 50px; }

/* Responsive CSS */
@media screen and (max-width: 700px) {
    #email { font-size: 17px; }
    .contact-logo { width: 40px; height: 40px; }
}
