/* common CSS */
* {
  box-sizing: border-box;
  font-family: 'Montserrat';  /* From fontOption*/
  word-break: break-word;
}

.App {
  text-align: center;
}

/* First left space of paragraph */
.paragraph-space {
  margin-left: 10px;
}

.section {
  display: flex; flex-direction: column;
  align-items: center;
  padding: 50px 40px 70px 40px;
}

.section-title {
  font-size: 40px;
  margin: 40px 0px;
}

.content-container {
  display: block; text-align: left;
  max-width: 800px; width: 80vw;
  padding: 0px 20px;  margin-bottom: 50px;
}

/* Responsive CSS */
@media screen and (max-width: 700px) {
  .section-title { font-size: 30px; }
  * { font-size: 14px; }
}
