/* modal scroll setup */
.ReactModal__Html--open,
.ReactModal__Body--open {
  overflow-y: hidden; /* prevents background page from scrolling when the modal is open */
}
.ReactModal__Overlay {
  position: fixed;
  z-index: 999999; top: 0; left: 0;
  width: 100vw; height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex; align-items: center; justify-content: center;
}
.ReactModal__Content {
  background: white;
  max-width: calc(100vw - 2rem); max-height: calc(100vh - 2rem);
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.25);
  overflow-y: auto;
}

/* contents */
.modal-box { 
  width: 70vw; max-width: 900px; 
  padding: 0px 10px;
}
.portfolioModal-img {
  width: 100%; height: 37vw; max-height: 415px;
}
.portfolioModal-text { 
  margin-bottom: 10px;
}
#portfolioModal-main-title {
  font-size: 32px;
}
.portfolioModal-text-subtitle {
  font-size: 22px; margin-top: 40px;
}

/* url Link css */
.portfolioModal-urlLink {
  color: blue; text-decoration: none;
}
.portfolioModal-urlLink:hover {
  font-weight: bold; color: rgb(13, 13, 177)
}
#githubRepo {
  margin-right: 20px;
}

/* ul padding set */
.portfolioModal-ul {
  padding-left: 20px;
}

/* carousel style */
.carousel-container {
  display: flex; flex-direction: column;
  align-items: center; justify-content: center;
  padding: 25px;
  width: 100%;
}
.carousel-caption {
  margin-top: 10px;
  font-size: 20px; color: white;
}

/* close button */
.portfolioModal-close-button {
  opacity: 0.4; display: block; float: right;
}
.portfolioModal-close-button:hover {
  cursor: pointer; opacity: 1.0;
}

/* responsive css */
@media screen and (max-width: 600px) {
  .carousel-caption { font-size: 15px; }
  #portfolioModal-main-title { font-size: 25px; }
  .portfolioModal-text-subtitle { font-size: 17px; }
  #portfolioModal-github-subtext { font-size: 15px;}
}

/* remove unnecessary spaces */
.thumbs-wrapper {
  width: 0px; height: 0px;
}
